import el from './element';
import diff from './diff';
import patch from './patch';

window.dvd = {
  el, diff, patch
};

var diffList = require("list-diff2")
var oldList = [{id: "a"}, {id: "b"}, {id: "c"}, {id: "d"}, {id: "e"}, {id: "g"}]
var newList = [{id: "c"}, {id: "a"}, {id: "b"}, {id: "g"}, {id: "d"}, {id: "f"}]
 
var {moves} = diffList(oldList, newList, "id")
moves.forEach(function(move) {
  if (move.type === 0) {
    oldList.splice(move.index, 1) // type 0 is removing
  } else {
    oldList.splice(move.index, 0, move.item) // type 1 is inserting
  }
});
console.log(moves);
console.log(oldList);